html {
  box-sizing: border-box;
  --moyo-blue: #00acdc;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  background-color: #e5e5e5;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background-color: #fff;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__content {
  flex-grow: 1;
  justify-content: flex-start;
  align-items: center;
  max-width: 1200px;
  margin: 0 50px 0 0;
  padding: 0;
  display: flex;
}

.logo {
  margin-left: 50px;
  margin-right: auto;
}

.logo img {
  width: auto;
  height: 40px;
  display: block;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  white-space: nowrap;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__item:first-child .nav__link {
  margin-left: 0;
}

.nav__link.is-active, .nav__link:hover {
  color: var(--moyo-blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--moyo-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 1024px) {
  .logo {
    margin-right: auto;
  }
}
